import { Component, OnInit } from '@angular/core';
import { AuthService } from '../services/auth.service';
import { Router } from '@angular/router';

@Component({
  selector: 'app-rs-login',
  templateUrl: './rs-login.component.html',
  styleUrls: ['./rs-login.component.css']
})
export class RsLoginComponent implements OnInit {

  constructor(private authService: AuthService, private router: Router,
    ) { }

  public mobileNumber: any;

  public submittedOTP: any;
  public encOTP: any;
  public isUserExists: boolean = false;
  public isMerchant: boolean = false;
  public displayOTP: boolean = false;

  ngOnInit() {

    let userDetails = this.authService.storeUserDetails();
    if (userDetails != undefined && userDetails != null) {
      if (userDetails.ismerchant) {
        this.router.navigate(['/rs-post-ads']);
      }
      else {
        this.router.navigate(['/']);
      }
    }
  }

  login() {
    this.authService.loginotprequest(this.mobileNumber).subscribe((data: any) => {
      this.encOTP = data;
      // store mobile number
      localStorage.setItem('mobileNumber', this.mobileNumber);
      this.displayOTP = true;
    }, (error) => {
    });
  }

  validateOTP() {
    // base64 encode
    var submittedEncryptedOTP = btoa(this.submittedOTP);

    if (this.encOTP.encryptotp == submittedEncryptedOTP) {
      if (!this.encOTP.isuserexists) {
        /// Redirect to Registration Screen
        this.router.navigate(['/rs-register']);
      }
      else {
        /// api/Login API call once receive Authkey store in session and call each API Request
        //login API
        this.authService.storeAuthKey();
        if (localStorage.getItem('userDetails') != undefined && localStorage.getItem('userDetails') != null) {
          let userJson = localStorage.getItem('userDetails');


          //Customer API
          //// verify this mobile no is merchant or induavial
          /// if mobile no merchant then go to post ad page
          if (userJson != undefined && userJson != null) {
            if (userJson['isMerchant']) {
              this.router.navigate(['/rs-post-ads']);              
            }
            else {
              this.router.navigate(['/rs-home']);              
            }

            /// else home screen
          }
        }
      }
      alert('Vaid');
    } else {
      alert("Invalid Otp");
    }
  }

}
