import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs';
import { environment } from 'src/environments/environment';

@Injectable({
  providedIn: 'root'
})
export class MasterRelatedService {

  constructor(private http: HttpClient) { }

  private _ad_brandcategoryUrl = "ad_brandcategory";
  private _ad_itemcategoryUrl = "ad_itemcategory";
  private _ad_linkbrandmaincategoryUrl = "ad_linkbrandmaincategory";
  private _ad_maincategoryUrl = "ad_maincategory";
  private _ad_subcategoryUrl = "ad_subcategory";

  private _masteradstatusUrl = "masteradstatus";
  private _masterbrandsUrl = "masterbrands";
  private _masterdataUrl = "masterdata";
  private _masteritemstatusUrl = "masteritemstatus";
  private _masterlinkitemcompanyUrl = "masterlinkitemcompany";
  private _mstitemcategoriesUrl = "mstitemcategories";
  private _mstmaincategoriesUrl = "mstmaincategories";
  private _mstsubcategoriesUrl = "mstsubcategories";

  // 6
  masteradstatus(): Observable<any> {
    return this.http.get(environment.baseUrl + this._masteradstatusUrl);
  }

  // 7
  masterbrands(): Observable<any> {
    return this.http.get(environment.baseUrl + this._masterbrandsUrl);
  }

  // 8
  masterdata(): Observable<any> {
    return this.http.get(environment.baseUrl + this._masterdataUrl);
  }
  
  // 1
  ad_brandcategory(id: any, name: any, mobileimgpath: any, webimgpath: any, isactive: any, desc: any): Observable<any> {
    let data = {
      id: id,
      name: name,
      mobileimgpath: mobileimgpath,
      webimgpath: webimgpath,
      isactive: isactive,
      desc: desc
    };
    return this.http.get(environment.baseUrl + this._ad_brandcategoryUrl, { params: data })
  }

  // 2
  ad_itemcategory(id: any, name: any, mobileimgpath: any, webimgpath: any, isactive: any, desc: any, mstsubcategoriesid: any): Observable<any> {
    let data = {
      id: id,
      name: name,
      desc: desc,
      mobileimgpath: mobileimgpath,
      webimgpath: webimgpath,
      isactive: isactive,
      mstsubcategoriesid: mstsubcategoriesid
    };
    return this.http.get(environment.baseUrl + this._ad_itemcategoryUrl, { params: data })
  }

  // 3
  ad_linkbrandmaincategory(id: any, mstcategoriesId: any, mstcompaniesId: any): Observable<any> {
    let data = {
      id: id,
      name: mstcategoriesId,
      desc: mstcompaniesId
    };
    return this.http.get(environment.baseUrl + this._ad_linkbrandmaincategoryUrl, { params: data })
  }

  // 4
  ad_maincategory(id: any, name: any, desc: any, mobileimgpath: any, webimgpath: any, isactive: any, getMstsubcategories: any, getMstcompanies: any): Observable<any> {
    let data = {
      id: id,
      name: name,
      desc: desc,
      mobileimgpath: mobileimgpath,
      webimgpath: webimgpath,
      isactive: isactive,
      getMstsubcategories: getMstsubcategories,
      getMstcompanies: getMstcompanies
    };
    return this.http.get(environment.baseUrl + this._ad_maincategoryUrl, { params: data })
  }

  // 5
  ad_subcategory(id: any, name: any, desc: any, mobileimgpath: any, webimgpath: any, isactive: any, mstMainCategoriesId: any, getMstitemcategories: any): Observable<any> {
    let data = {
      id: id,
      name: name,
      desc: desc,
      mobileimgpath: mobileimgpath,
      webimgpath: webimgpath,
      isactive: isactive,
      mstMainCategoriesId: mstMainCategoriesId,
      getMstitemcategories: getMstitemcategories
    };
    return this.http.get(environment.baseUrl + this._ad_subcategoryUrl, { params: data })
  }

  // 9
  masteritemstatus(): Observable<any> {
    return this.http.get(environment.baseUrl + this._masteritemstatusUrl);
  }

  // 10
  masterlinkitemcompany(): Observable<any> {
    return this.http.get(environment.baseUrl + this._masterlinkitemcompanyUrl);
  }

  // 11
  mstitemcategories(): Observable<any> {
    return this.http.get(environment.baseUrl + this._mstitemcategoriesUrl);
  }

  // 12
  mstmaincategories(): Observable<any> {
    return this.http.get(environment.baseUrl + this._mstmaincategoriesUrl);
  }

  // 13
  mstsubcategories(): Observable<any> {
    return this.http.get(environment.baseUrl + this._mstsubcategoriesUrl);
  }

}
