import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { RsHomeComponent } from './rs-home/rs-home.component';
import { RsLoginComponent } from './rs-login/rs-login.component';
import { RsRegisterComponent } from './rs-register/rs-register.component';
import { RsAccountFavouriteAdsComponent } from './rs-account-favourite-ads/rs-account-favourite-ads.component';
import { RsAccountMyadsComponent } from './rs-account-myads/rs-account-myads.component';
import { RsAccountProfileSettingComponent } from './rs-account-profile-setting/rs-account-profile-setting.component';
import { RsAdsDetailsComponent } from './rs-ads-details/rs-ads-details.component';
import { RsDashboardComponent } from './rs-dashboard/rs-dashboard.component';
import { RsFaqComponent } from './rs-faq/rs-faq.component';
import { RsOffermessagesComponent } from './rs-offermessages/rs-offermessages.component';
import { RsPaymentsComponent } from './rs-payments/rs-payments.component';
import { RsPostAdsComponent } from './rs-post-ads/rs-post-ads.component';
import { RsPricingComponent } from './rs-pricing/rs-pricing.component';
import { RsPrivacySettingComponent } from './rs-privacy-setting/rs-privacy-setting.component';
import { RsSinglePostComponent } from './rs-single-post/rs-single-post.component';

const routes: Routes = [
  { path: '', component: RsLoginComponent ,pathMatch:"full"},
  { path: 'rs-login', component: RsLoginComponent },
  { path: 'rs-register', component: RsRegisterComponent },
  { path: 'rs-home', component: RsHomeComponent },
  { path: 'rs-account-favourite-ads', component: RsAccountFavouriteAdsComponent },
  { path: 'rs-account-myads', component: RsAccountMyadsComponent },
  { path: 'rs-account-profile-setting', component: RsAccountProfileSettingComponent },
  { path: 'rs-ads-details', component: RsAdsDetailsComponent },
  { path: 'rs-dashboard', component: RsDashboardComponent },
  { path: 'rs-faq', component: RsFaqComponent },
  { path: 'rs-offermessages', component: RsOffermessagesComponent },
  { path: 'rs-payments', component: RsPaymentsComponent },
  { path: 'rs-post-ads', component: RsPostAdsComponent },
  { path: 'rs-pricing', component: RsPricingComponent },
  { path: 'rs-privacy-setting', component: RsPrivacySettingComponent },
  { path: 'rs-single-post', component: RsSinglePostComponent }
];

@NgModule({
  imports: [RouterModule.forRoot(routes, { useHash: false })],
  exports: [RouterModule]
})
export class AppRoutingModule { }
