import { Component, OnInit } from '@angular/core';
import { DeviceDetectorService } from 'ngx-device-detector';

import { AuthService } from '../services/auth.service';

@Component({
  selector: 'app-rs-register',
  templateUrl: './rs-register.component.html',
  styleUrls: ['./rs-register.component.css']
})
export class RsRegisterComponent implements OnInit {

  constructor(private authService: AuthService,
    private deviceService: DeviceDetectorService,
    ) {
    this.getDeviceInfo();
  }

  public userJson = {
    "customerId": "",
    "name": "",
    "image": "",
    "address": "",
    "landmark": "",
    "city": "",
    "gstNo": "",
    "eMail": "",
    "phoneNo": localStorage.getItem('mobileNumber'),
    "whatsappno": localStorage.getItem('mobileNumber'),
    "password": "",
    "latitude": localStorage.getItem('latitude'),
    "longitude": localStorage.getItem('longitude'),
    "isActive": true,
    "isMerchant": true,
    "cdt": new Date().toISOString(),
    "mdt": new Date().toISOString(),
    "reason": ""
  }

  public responseUserJson: any;

  public deviceInfo: any;
  public authKey: any;

  ngOnInit() {
  }

  // register new user step 1
  registerUser() {
    this.authService.register(this.userJson).subscribe((data: any) => {
      console.log(data)
      this.responseUserJson = data;
      this.getAuthKey();
    }, (error) => {
    });
  }

  // get authentication key step 2
  getAuthKey() {
    let json = {
      "userid": localStorage.getItem('mobileNumber'),
      "password": "",
      "deviceinfo": {
        "deviceInfoId": "",
        "customerId": this.responseUserJson.customerId,
        "deviceType": "web",
        "deviceId": this.deviceInfo.os_version,
        "secrateKey": "",
        "expiredOn": this.responseUserJson.mdt,
        "cdt": this.responseUserJson.cdt,
        "isActive": true
      }
    }
    this.authService.getAuthToken(json).subscribe((data: any) => {
      console.log(data)
      this.authKey = data;
      // store authentication key
      localStorage.setItem('AuthKey', this.authKey);
    }, (error) => {
    });
  }

  // get device info
  getDeviceInfo() {
    this.deviceInfo = this.deviceService.getDeviceInfo();
    console.log(this.deviceInfo);
  }

}
