import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs';
import { environment } from '../../environments/environment';
import { DeviceDetectorService } from 'ngx-device-detector';

@Injectable({
  providedIn: 'root'
})
export class AuthService {

  constructor(private http: HttpClient, private deviceService: DeviceDetectorService) { this.getDeviceInfo()}

  private userDetails: any;
  private apiResponse: any;
  private deviceInfo: any;
  private authKey: any;

  private _loginotprequestUrl = 'loginotprequest';
  private _registerUrl = 'register';
  private _loginUrl = 'login';
  private _cusomerauthUrl = 'cusomerauth';

  loginotprequest(mobileNo): Observable<any> {
    let data = { mobileno: mobileNo };
    return this.http.get(environment.baseUrl + this._loginotprequestUrl, { params: data })
  }

  register(userJson): Observable<any> {
    return this.http.post(environment.baseUrl + this._registerUrl, userJson)
  }

  getAuthToken(userJson): Observable<any> {
    return this.http.post(environment.baseUrl + this._loginUrl, userJson)
  }

  getUserDetails(customerId): Observable<any> {
    return this.http.get(environment.baseUrl + this._cusomerauthUrl + '/' + customerId)
  }

  // get auth key
  storeAuthKey() {
    let json = {
      "userid": localStorage.getItem('mobileNumber'),
      "password": "",
      "deviceinfo": {
        "deviceInfoId": "",
        "customerId": "",
        "deviceType": "web",
        "deviceId": this.deviceInfo.os_version,
        "secrateKey": "",
        "expiredOn": new Date().toISOString(),
        "cdt": new Date().toISOString(),
        "isActive": true
      }
    }
    this.getAuthToken(json).subscribe((data: any) => {
      console.log(data)
      this.authKey = data;
      // store authentication key
      localStorage.setItem('AuthKey', this.authKey);
      this.storeUserDetails();
    }, (error) => {
    });
  }

  // get user details
  storeUserDetails() {
    if (localStorage.getItem('AuthKey') != undefined && localStorage.getItem('AuthKey') != null) {
      this.getUserDetails(localStorage.getItem('mobileNumber')).subscribe((data: any) => {
        this.userDetails = data;
        // store user details
        localStorage.setItem('userDetails', JSON.stringify(this.userDetails));
        return this.userDetails;
      });
    }
    return this.userDetails
  }

  // get device info
  getDeviceInfo() {
    this.deviceInfo = this.deviceService.getDeviceInfo();
    console.log(this.deviceInfo);
  }

}
