import { Component, OnInit } from '@angular/core';

import { MasterRelatedService } from '../services/master-related.service';
import { AdRelatedService } from '../services/ad-related.service';

import * as moment from "moment";

@Component({
  selector: 'app-rs-post-ads',
  templateUrl: './rs-post-ads.component.html',
  styleUrls: ['./rs-post-ads.component.css']
})

export class RsPostAdsComponent implements OnInit {

  constructor(private masterRelatedService: MasterRelatedService,
    private adRelatedService: AdRelatedService) { }

  public masteritemstatusList: any = [];
  public masterData: any = [];
  public subCategoryList: any = [];
  public itemCategoryList: any = [];
  public masterbrandsList: any = [];
  public imageNamesList = [];

  public urls = new Array<string>();
  public uploadedFiles: any = [];
  public agreeTR: boolean = false;

  public adJson = {
    "id": "",
    "adSeqId": 0,
    "adName": "",
    "adDesc": "",
    "customerId": "",
    "cdt": new Date().toISOString(),
    "adstatusId": 0,
    "mstmaincategories_id": 0,
    "mstsubcategories_id": 0,
    "mstitemcategories_id": 0,
    "expiredOn": moment(moment(new Date()).add('days', 20)).format('DD-MM-YYYY'),
    "distance": 0,
    "latitude": localStorage.getItem('latitude'),
    "longitude": localStorage.getItem('longitude'),
    "address": "",
    "city": "",
    "metaData": "",
    "price": 0,
    "canCallOnly": false,
    "yearOfMfg": 0,
    "mstcompanies_id": 0,
    "isNegotiable": true,
    "isDisplayPersonalDetails": false,
    "adimagelist": "",
    "itemstatuId": 0,
    "isindividualad": 0,
    "adimglist": []
  }

  ngOnInit() {
    this.masterData = JSON.parse(localStorage.getItem('masterData'));
    this.getmasteritemstatus();
    let userJson = JSON.parse(localStorage.getItem('userDetails'));
    console.log(userJson);
    this.adJson.address = userJson['address'];
    this.adJson.city = userJson['city'];
    this.adJson.customerId = userJson['customerId'];
  }

  // get master item status
  getmasteritemstatus() {
    this.masterRelatedService.masteritemstatus().subscribe((data: any) => {
      this.masteritemstatusList = data;
    }, (error) => {
    });
  }

  // get sub categories
  getSubcategoryList(event) {
    this.subCategoryList = [];
    let length = this.masterData.length;
    for (let index = 0; index < length; index++) {
      if (event == this.masterData[index].id) {
        this.subCategoryList = this.masterData[index].mstsubcategorylist;
        break;
      }
    }
  }

  // get item sub categories
  getItemSubcategoryList(event) {
    this.itemCategoryList = [];
    let length = this.subCategoryList.length
    for (let index = 0; index < length; index++) {
      if (event == this.subCategoryList[index].id) {
        this.itemCategoryList = this.subCategoryList[index].getMstitemcategorylist;
        break;
      }
    }
  }

  // get master brands
  getmasterbrands() {
    this.masterbrandsList = [];
    let length = this.masterData.length;
    for (let index = 0; index < length; index++) {
      if (this.adJson.mstmaincategories_id == this.masterData[index].id) {
        this.masterbrandsList = this.masterData[index].mstcompanylist;
        break;
      }
    }
  }
  
  detectFiles(event) {
    if (event.target.files.length > 5) {
      alert("Only 5 files accepted.");
      event.preventDefault();
    } else {
      this.urls = [];
      for (var i = 0; i < event.target.files.length; i++) {
        this.uploadedFiles.push(event.target.files[i]);
      }
      if (this.uploadedFiles) {
        for (let file of this.uploadedFiles) {
          let reader = new FileReader();
          reader.onload = (e: any) => {
            this.urls.push(e.target.result);
          }
          reader.readAsDataURL(file);
        }
      }
      console.log(this.uploadedFiles)
    }
  }

  saveadJson() {
    this.adJson.adimglist = [];
    // let index = 0;
    // for (const key of this.uploadedFiles) {
    //   console.log(key)
    //   index++;
    //   this.adJson.adimglist.push({
    //     "id": index,
    //     "seqid": index,
    //     "imgpath": key.name
    //   });
    //   this.imageNamesList.push(key.name);
    // }
    this.adRelatedService.adimgpost(this.uploadedFiles).subscribe((data: any) => {
      console.log(data);
      this.adJson.adimglist = data;
      this.saveJson();
    }, (error) => {
    });
  }
  
  saveJson() {
    this.adRelatedService.newadpost(this.adJson).subscribe((data: any) => {
      console.log(data);
    }, (error) => {
    });
  }

}
