import { Component, OnInit } from '@angular/core';
import { MasterRelatedService } from '../services/master-related.service';

@Component({
  selector: 'app-rs-home',
  templateUrl: './rs-home.component.html',
  styleUrls: ['./rs-home.component.css']
})
export class RsHomeComponent implements OnInit {

  constructor(private masterRelatedService: MasterRelatedService) { }

  public masterData: any;

  ngOnInit() {
    this.getMasterData();
  }

  getMasterData() {
    if (localStorage.getItem('masterData') == undefined) {
      this.masterRelatedService.masterdata().subscribe((data: any) => {
        this.masterData = data;
        // store master data
        localStorage.setItem('masterData', JSON.stringify(this.masterData));
      }, (error) => {
      });
    }
  }

}
