import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Observable } from 'rxjs';
import { environment } from 'src/environments/environment';

@Injectable({
  providedIn: 'root'
})
export class AdRelatedService {

  constructor(private http: HttpClient) { }

  private _adInfoUrl = "adinfo";
  private _newadpostUrl = "newadpost";
  private _quicklocationsearchUrl = "quicklocationsearch";
  private _searchrelatedadlistUrl = "searchrelatedadlist";
  private _adimgpostUrl = "adimgpost";

  // 1
  adinfo(adid: any): Observable<any> {
    let data = { adid: adid };
    return this.http.get(environment.baseUrl + this._adInfoUrl, { params: data })
  }

  // 2
  newadpost(adJson: any): Observable<any> {
    return this.http.post(environment.baseUrl + this._newadpostUrl, adJson)
  }

  // 3
  quicklocationsearch(searchname: any): Observable<any> {
    let data = { searchname: searchname };
    return this.http.get(environment.baseUrl + this._quicklocationsearchUrl, { params: data })
  }

  // 4
  searchrelatedadlist(): Observable<any> {
    return this.http.get(environment.baseUrl + this._searchrelatedadlistUrl)
  }

  // 5
  adimgpost(fileName): Observable<any> {
    const formData = new FormData();
    for (var i = 0; i < fileName.length; i++) {
      formData.append("file[]", fileName[i]);
    }
    return this.http.post(environment.baseUrl + this._adimgpostUrl, formData)
  }
}
